import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams,LoadingController} from 'ionic-angular';
import {Observable} from "rxjs-compat";
import {YtProvider} from "../../providers/yt/yt";

interface Live {
  docid: string;
  title: string,

}


@IonicPage()
@Component({
  selector: 'page-livestream',
  templateUrl: 'livestream.html',
})
export class LivestreamPage {

  channelId = 'UC0qRTBa4OzIoiu8nfs9A2LA';
  playlists: Observable<any[]>;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              private loadingCtrl: LoadingController,
              private ytProvider: YtProvider,
              ) {

  }


  ionViewDidLoad() {
    this.loadData()
  }
  loadData(){
    const loader = this.loadingCtrl.create({
      content: "Please Wait",
      dismissOnPageChange:true,
    });
loader.present()

      this.playlists = this.ytProvider.getPlaylistsForChannel(this.channelId);
      this.playlists.subscribe(data => {
        loader.dismissAll()

      }, err => {


      })
    }

    openPlaylist(id) {
      this.navCtrl.push('PlaylistPage', {id: id});
    }




}



